import Nav from '../elements/Nav'
import Foot from '../elements/Footer'
import Pricing from '../elements/Pricing'
import React, { Component } from 'react';
import banner from '../media/img/banner.png'
import TestComponent from './vidIndex'
// import ViewOne from '../views/Lists';
// import Home from '../json/Home'
// import Player from '../elements/vidIndex'
import vid from '../media/vid/lhp_movie.mp4'
class Player extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isPlaying: undefined,
      isMuted: undefined,
      progress: 0,
      currentTime: 0,
      duration: 0,
      windowWidth: window.innerWidth,
      windowHeight: window.innerHeight,
    }
  }

  componentDidMount() {
    this.setState({
      isPlaying: !this.player.isPaused,
      isMuted: this.player.isMuted,
    });
    window.addEventListener('resize', this.handleResize);
  }

  componentWillMount() {
    window.removeEventListener('resize', this.handleResize);
  }

  handleResize = () => {
    this.setState({
      windowWidth: window.innerWidth,
      windowHeight: window.innerHeight,
    })
  };

  handleOnPlay = () => {
    this.setState({isPlaying: true});
  };

  handleOnPause = () => {
    this.setState({isPlaying: false});
  };

  handleTimeUpdate = (currentTime, progress, duration) => {
    this.setState({
      progress,
      currentTime,
      duration,
    });
  };

  handleOnMute = () => {
    this.setState({isMuted: true});
  };

  handleOnUnmute = () => {
    this.setState({isMuted: false});
  };

  togglePlay = () => {
    this.player.togglePlay();
  };

  toggleMute = () => {
    this.player.toggleMute();
  };

  render() {
    return (
      <div className="video-container">
        <TestComponent
          ref={p => this.player = p}
          containerWidth={this.state.windowWidth}
          containerHeight={this.state.windowHeight}
          src={vid}
          poster={banner}
          onPlay={this.handleOnPlay}
          onPause={this.handleOnPause}
          onMute={this.handleOnMute}
          onUnmute={this.handleOnUnmute}
          onTimeUpdate={this.handleTimeUpdate}
          startTime={10}
          autoPlay={true}
          volume={0.5}
        />
        <span style={{
          position: 'absolute',
          left: 30,
          bottom: 30,
          display: 'flex',
          alignItems: 'center',
        }}>
          {/* <button style={{width: 70}} onClick={this.togglePlay}>
            {this.state.isPlaying ? 'Pause' : 'Play'}
          </button>
          <progress value={this.state.progress}/>
          <button style={{width: 70}} onClick={this.toggleMute}>
            {this.state.isMuted ? 'Unmute' : 'Mute'}
          </button> */}
          {/* <time>{this.state.currentTime} / {this.state.duration}</time> */}
        </span>
      </div>
    )
  }
}
export default function App() {
  return (<span>
    <Nav/>
    <Player />
    <br/><br/>
    <Pricing/>
    <br/><br/>
    <Foot/>
  </span>
  );
}